import React, {useEffect} from 'react'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import styled from 'styled-components'
import Typical from 'react-typical'
import Seguridad from '../assets/img/seguridadPreview.png'
import Memo from '../assets/img/memotestPreview.png'
import AOS from "aos";
const WorkHistory = () => {
  useEffect(() => {
    setTimeout(() => {
      AOS.init({ duration: 1100, once: true })
    }, 1000)
  }, [])
  return (
    <div id="Projects">
      <Typical
        className="text-center mt-6 mb-6"
        steps={['Recent work:', 2500, 'More Coming soon... </>']}
        loop={Infinity}
        wrapper="h2"
      />
      <Container>
        <a
          data-aos="flip-right"
          href="https://seguridad-itisjt.tk"
          target="_blank"
        >
          <img src={Seguridad} alt="" />
        </a>
        <a
          data-aos="flip-left"
          href="https://javascript-memotest.netlify.app/"
          target="_blank"
        >
          <img src={Memo} alt="" />
        </a>
      </Container>
    </div>
  )}

const Container = styled.div`
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
  padding: 2rem;
  a { 
    width: fit-content;
    margin: 0 auto;
  }
  img {
    display: flex;
    margin: 0 auto;
    border-radius: 0.4rem;
    box-shadow: 1px 2px 7px #242424;
    width: 500px;
    transition: all 0.2s ease;
    margin-bottom: 1rem;
  }
  img:hover {
    transform: scale(1.1);
  } 
`

export default WorkHistory
